import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)
// 引入一级路由
const login=()=>Promise.resolve(import("../page/login"))
const index=()=>Promise.resolve(import("../page/index"))
const movie=()=>Promise.resolve(import("../page/movie"))
const movieDetail=()=>Promise.resolve(import("../page/movieDetail"))
const food=()=>Promise.resolve(import("../page/food"))
const foodDetail=()=>Promise.resolve(import("../page/foodDetail"))
// import login from "../page/login"
// import index from "../page/index"
// import movie from "../page/movie"
// import movieDetail from "../page/movieDetail"
// import food from "../page/food"
// import foodDetail from "../page/foodDetail"

// 引入二级路由
const home=()=>import("../components/home")
const order=()=>import("../components/order")
const mine=()=>import("../components/mine")
// import home from "../components/home"
// import order from "../components/order"
// import mine from "../components/mine"

// 引入公共组件路由
const search=()=>import("../common/vSearch")
// import search from "../common/vSearch"

const router =  new Router({
  // 路由模式
  mode:"hash",
  // 配置路由规则
  routes: [
    {
      path:"/login",
      name:"登录",
      component:login
    },
    {
      path:"/index",
      component:index,
      children:[
        {
          path:"home",
          name:"/index/home",
          component:home
        },
        {
          path:"order",
          name:"/index/order",
          component:order
        },
        {
          path:"mine",
          name:"/index/mine",
          component:mine
        },
        {
          path:"",
          redirect: "home"
        }
      ]
    },
    {
      path:"/movie",
      name:"电影",
      component:movie
    },
    {
      path:"/movieDetail",
      name:"电影详情",
      component:movieDetail
    },
    {
      path:"/food",
      name:"外卖",
      component:food,
      beforeEnter(to,from,next){
        // 取出status是否为1，如果为1说明是外卖小哥，那么就放他进来。
        var status = sessionStorage.getItem("status")
        if(status==="1"){
          next()
        }else{
          next("/login")
        }
      }
    },
    {
      path:"/foodDetail/:id",
      name:"外卖详情",
      component:foodDetail,
      beforeEnter(to,from,next){
        // 取出status是否为1，如果为1说明是外卖小哥，那么就放他进来。
        var status = sessionStorage.getItem("status")
        if(status==="1"){
          next()
        }else{
          next("/login")
        }
      }
    },
    {
      path:"/search",
      // 别名
      alias:"/s",
      component:search
    },
    {
      path:"*",
      redirect: "/login"
    }
  ]
})
//beforeEach()进来之前
router.beforeEach((to,from,next)=>{
  // 如果去的是/login页面就直接进
  if(to.path==="/login"){
    next()
    return
  }
  //如果去的不是登录页面，就要判断是否登录过。如果登录过了那么就能正常访问，如果没有登陆过那么就不能访问
  var isLogin = sessionStorage.getItem("isLogin")
  if(isLogin){
    next()
    return
  }
})
//afterEach()离开之后
router.afterEach(()=>{
  console.log('我离开了');
})





export default router


